import { createRouter, createWebHistory } from "vue-router";
import Home from "../views/HomeMain.vue";
import Login from "../views/Login.vue";
import Register from "../views/Register.vue";

import Profile from "../views/Profile.vue";
import BoardAdmin from "../views/admin/BoardAdmin.vue";
import BoardModerator from "../views/mod/BoardModerator.vue";
import BoardUser from "../views/BoardUser.vue";
import Public from "../views/BoardPublic.vue";
const Tasks = () => import("../views/Tasks.vue");

const NotFound = () => import("@/views/NotFound.vue");

const routes = [
  {
    path: "/",
    component: Home,
    children: [
      {
        path: "/profile",
        name: "profile",
        components: { profile: Profile },
      },
      {
        path: "/public",
        name: "public",
        components: { public: Public },
      },
      {
        path: "/tasks",
        name: "tasks",
        components: { tasks: Tasks },
      },
    ],
  },
  {
    path: "/home",
    name: "home",
    component: Home,
  },
  { path: "/login", name: "login", component: Login },
  {
    path: "/register",
    component: Register,
  },
  {
    path: "/board-admin",
    name: "boardAuthorizated",
    component: BoardAdmin,
  },
  {
    path: "/board-mod",
    name: "moderator",
    component: BoardModerator,
  },
  {
    path: "/board-user",
    name: "user",
    component: BoardUser,
  },

  {
    path: "/",
    alias: "/tutorials",
    name: "tutorials",
    component: () => import("../views/tutorial/TutorialsList"),
  },
  {
    path: "/tutorials/:id",
    name: "tutorial-details",
    component: () => import("../views/tutorial/Tutorial"),
  },
  {
    path: "/add",
    name: "add",
    component: () => import("../views/tutorial/AddTutorial"),
  },

  { path: "/:pathMatch(.*)*", name: "NotFound", component: NotFound },
];

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes,
});

export default router;
